.merocorporate__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.merocorporate__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.merocorporate__navbar-links_logo {
    margin-right: 2rem;
}

.merocorporate__navbar-links_logo img {
    width: 60px;
    height: 60px;
}

.merocorporate__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.merocorporate__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.merocorporate__navbar-links_container p,
.merocorporate__navbar-sign p,
.merocorporate__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.merocorporate__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.merocorporate__navbar-menu svg {
    cursor: pointer;
}

@media screen and (max-width: 1050px) {
    .merocorporate__navbar-links_container {
        display: none;
    }

    .merocorporate__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .merocorporate__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .merocorporate__navbar {
        padding: 2rem;
    }

    .merocorporate__navbar-sign {
        display: none;
    }
    
    .merocorporate__navbar-menu_container-links-sign {
        display: block;
    }
}
